<template>
  <section id="alarm-list-page" class="df">
    <div class="left-tree tree">
      <tree :nodes="nodes" :setting="treeSetting" ref="tree" />
    </div>
    <div class="right-content">
      <el-tabs v-model="curView" @tab-click="">
        <el-tab-pane label="活动告警" name="1">
          <div class="tab-content df dfv">
            <div class="query-form df">
              <el-form :inline="true" :label-width="'100px'" style="max-width: 1300px;">
                <el-form-item :label="'告警名称'" prop="upSize">
                  <el-input size="mini" v-model="activeAlarmQuery.upSize"></el-input>
                </el-form-item>
                
                <el-form-item :label="'告警等级'" prop="upSize">
                  <el-select size="mini" v-model="activeAlarmQuery.upSize">
                    <el-option v-for="(item, index) in []" :key="index" :label="item" :value="item">{{ item }}</el-option>
                  </el-select>
                </el-form-item>
                
                <el-form-item :label="'告警状态'" prop="upSize">
                  <el-select size="mini" v-model="activeAlarmQuery.upSize">
                    <el-option v-for="(item, index) in []" :key="index" :label="item" :value="item">{{ item }}</el-option>
                  </el-select>
                </el-form-item>

                <el-form-item :label="'确认状态'" prop="upSize">
                  <el-select size="mini" v-model="activeAlarmQuery.upSize">
                    <el-option v-for="(item, index) in []" :key="index" :label="item" :value="item">{{ item }}</el-option>
                  </el-select>
                </el-form-item>

                <el-form-item :label="'设备类型'" prop="upSize">
                  <el-input size="mini" v-model="activeAlarmQuery.upSize"></el-input>
                </el-form-item>
                
                <el-form-item :label="'设备型号'" prop="upSize">
                  <el-select size="mini" v-model="activeAlarmQuery.upSize">
                    <el-option v-for="(item, index) in []" :key="index" :label="item" :value="item">{{ item }}</el-option>
                  </el-select>
                </el-form-item>
                
                <el-form-item :label="'设备名称'" prop="upSize">
                  <el-select size="mini" v-model="activeAlarmQuery.upSize">
                    <el-option v-for="(item, index) in []" :key="index" :label="item" :value="item">{{ item }}</el-option>
                  </el-select>
                </el-form-item>

                <br/>
                
                <el-form-item :label="'告警发生时间'" prop="upSize">
                  <el-date-picker
                    v-model="activeAlarmQuery.upSize"
                    type="datetime"
                    size="mini" 
                    placeholder="选择日期时间">
                  </el-date-picker>
                </el-form-item>
                
                <el-form-item :label="'-　　　'" prop="upSize">
                  <el-date-picker
                    v-model="activeAlarmQuery.upSize"
                    type="datetime"
                    size="mini" 
                    placeholder="选择日期时间">
                  </el-date-picker>
                </el-form-item>

                <el-button type="primary" size="mini" @click="" style="margin-left: 33px;">确认</el-button>
                <el-button type="primary" size="mini" @click="">清除</el-button>
              </el-form>
            </div>

            <div class="list df dfv">
              <div class="list-header">
                <h3>活动告警列表</h3>
                <div class="btn-group">
                  <el-button type='primary' size="mini" @click="additionalInfoToModal()">告警拓展信息</el-button>
                  <el-button type='primary' size="mini" @click="alarmConfirmToModal()">告警确认</el-button>
                  <custom-column v-model="activeColumnList" :name="'alarmListCustomColumn'">自定义列</custom-column>
                </div>
              </div>
              <div class="table-box df dfv">
                <custom-table ref="activeAlarmList" :data="activeAlarmList" :columns="columnList" height="100%"></custom-table>

                <!-- Pagination -->
                <el-col :span="24" class="toolbar">
                  <el-pagination
                    layout="total, prev, pager, next,sizes, jumper"
                    @current-change="activePagi.handleCurrentChange"
                    @size-change="activePagi.pageSizeChange"
                    :page-sizes="[10, 15, 50, 100]"
                    :page-size="activePagi.pageSize"
                    :total="activePagi.total"
                    style="float:right;">
                  </el-pagination>
                </el-col>
              </div>
            </div>
          </div>
        </el-tab-pane>
        <el-tab-pane label="历史告警" name="second">
          <div class="tab-content df dfv">
            <div class="query-form df">
              <el-form :inline="true" :label-width="'100px'" style="max-width: 1350px;">
                <el-form-item :label="'告警名称'" prop="upSize">
                  <el-input size="mini" v-model="historyAlarmQuery.upSize"></el-input>
                </el-form-item>
                
                <el-form-item :label="'告警等级'" prop="upSize">
                  <el-select size="mini" v-model="historyAlarmQuery.upSize">
                    <el-option v-for="(item, index) in []" :key="index" :label="item" :value="item">{{ item }}</el-option>
                  </el-select>
                </el-form-item>
                
                <el-form-item :label="'告警状态'" prop="upSize">
                  <el-select size="mini" v-model="historyAlarmQuery.upSize">
                    <el-option v-for="(item, index) in []" :key="index" :label="item" :value="item">{{ item }}</el-option>
                  </el-select>
                </el-form-item>

                <el-form-item :label="'确认状态'" prop="upSize">
                  <el-select size="mini" v-model="historyAlarmQuery.upSize">
                    <el-option v-for="(item, index) in []" :key="index" :label="item" :value="item">{{ item }}</el-option>
                  </el-select>
                </el-form-item>

                <el-form-item :label="'设备类型'" prop="upSize">
                  <el-input size="mini" v-model="historyAlarmQuery.upSize"></el-input>
                </el-form-item>
                
                <el-form-item :label="'设备型号'" prop="upSize">
                  <el-select size="mini" v-model="historyAlarmQuery.upSize">
                    <el-option v-for="(item, index) in []" :key="index" :label="item" :value="item">{{ item }}</el-option>
                  </el-select>
                </el-form-item>
                
                <el-form-item :label="'设备名称'" prop="upSize">
                  <el-select size="mini" v-model="historyAlarmQuery.upSize">
                    <el-option v-for="(item, index) in []" :key="index" :label="item" :value="item">{{ item }}</el-option>
                  </el-select>
                </el-form-item>

                <br/>
                
                <el-form-item :label="'告警发生时间'" prop="upSize">
                  <el-date-picker
                    v-model="historyAlarmQuery.upSize"
                    type="datetime"
                    size="mini" 
                    placeholder="选择日期时间">
                  </el-date-picker>
                </el-form-item>
                
                <el-form-item :label="'-　　　'" prop="upSize">
                  <el-date-picker
                    v-model="historyAlarmQuery.upSize"
                    type="datetime"
                    size="mini" 
                    placeholder="选择日期时间">
                  </el-date-picker>
                </el-form-item>

                <el-form-item :label="'告警清除时间'" prop="upSize">
                  <el-date-picker
                    v-model="historyAlarmQuery.upSize"
                    type="datetime"
                    size="mini" 
                    placeholder="选择日期时间">
                  </el-date-picker>
                </el-form-item>
                
                <el-form-item :label="'-　　　'" prop="upSize">
                  <el-date-picker
                    v-model="historyAlarmQuery.upSize"
                    type="datetime"
                    size="mini" 
                    placeholder="选择日期时间">
                  </el-date-picker>
                </el-form-item>

                <el-button type="primary" size="mini" @click="" style="margin-left: 33px;">确认</el-button>
                <el-button type="primary" size="mini" @click="">清除</el-button>
              </el-form>
            </div>

            <div class="list df dfv">
              <div class="list-header">
                <h3>历史告警列表</h3>
                <div class="btn-group">
                  <el-button type='primary' size="mini" @click="additionalInfoToModal()">告警拓展信息</el-button>
                  <custom-column v-model="historyColumnList" :name="'alarmHistoryListCustomColumn'">自定义列</custom-column>
                </div>
              </div>
              <div class="table-box df dfv">
                <custom-table ref="historyAlarmList" :data="historyAlarmList" :columns="computedHistoryColumnList" height="100%"></custom-table>

                <!-- Pagination -->
                <el-col :span="24" class="toolbar">
                  <el-pagination
                    layout="total, prev, pager, next,sizes, jumper"
                    @current-change="historyPagi.handleCurrentChange"
                    @size-change="historyPagi.pageSizeChange"
                    :page-sizes="[10, 15, 50, 100]"
                    :page-size="historyPagi.pageSize"
                    :total="historyPagi.total"
                    style="float:right;">
                  </el-pagination>
                </el-col>
              </div>
            </div>
          </div>
        </el-tab-pane>
      </el-tabs>

    </div>  
    <el-dialog
      title="扩展信息添加"
      class="m400"
      :close-on-click-modal = "false"
      @close="additionalInfoVisible = false"
      :visible="additionalInfoVisible">
      <el-form :model="additionalInfoForm" :rules="additionalInfoRules" label-width="90px" ref="additionalInfo">
        <el-form-item label="企业名称" prop="enterpriseCode">
          <el-select size="mini" v-model="additionalInfoForm.enterpriseCode" style="width:250px">
            <el-option v-for="(item, index) in enterPriseList" :key="index" :label="item.title" :value="item.enterpriseCode">{{ item.title }}</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="服务名称" prop="serverCode">
          <el-select size="mini" v-model="additionalInfoForm.serverCode" style="width:250px">
            <el-option v-for="(item, index) in getServerList(additionalInfoForm.enterpriseCode)" :key="index" :label="item.title" :value="item.code">{{ item.title }}</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="中文名称" prop="str">
          <el-input v-model="additionalInfoForm.str" size="mini" style="width:250px"></el-input>
        </el-form-item>
        <el-form-item label="英文名称" prop="name">
          <el-input v-model="additionalInfoForm.name" size="mini" style="width:250px"></el-input>
        </el-form-item>
        <el-form-item label="字段类型" prop="type">
          <el-select size="mini" v-model="additionalInfoForm.type" style="width:250px">
            <el-option v-for="(item, index) in ['String', 'number']" :key="index" :label="item" :value="item">{{ item }}</el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" type="default" @click="additionalInfoVisible = false">取 消</el-button>
        <el-button size="mini" type="primary" @click="additionalInfoSubmit();">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="告警确认"
      class="m400"      
      :close-on-click-modal = "false"
      @close="alarmConfirmVisible = false"
      :visible="alarmConfirmVisible">
      <el-form :model="alarmConfirmForm" :rules="alarmConfirmRules" label-width="90px" ref="alarmConfirm">
        <el-form-item label="用户姓名" prop="machineType">
          <el-input v-model="alarmConfirmForm.machineType" size="mini" style="width:250px"></el-input>
        </el-form-item>
        <el-form-item label="操作时间" prop="aaa">
          <el-input v-model="alarmConfirmForm.aaa" size="mini" style="width:250px"></el-input>
        </el-form-item>
        <el-form-item label="确认内容" prop="bbb">
          <el-input v-model="alarmConfirmForm.aaa" :rows="3" resize="none" type="textarea" style="width:250px"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" type="default" @click="alarmConfirmVisible = false">取 消</el-button>
        <el-button size="mini" type="primary" @click="alarmConfirmVisible = false;">确 定</el-button>
      </span>
    </el-dialog>

  </section>
</template>

<script>
import { customTable, resolveScopedSlots } from "./customColumn.vue";
import customColumn from './customColumn.vue'
export default {
  name: 'alarm-list',
  components: {
      customColumn,
      customTable,
  },
  data () {
    let scope = this;
    return {
      curEnterprise: {code: 'meitainuo'},
      curServer: {code: 'zhyd'},
      enterPriseList: [],
      nodes: [{
        name: '全部',
        children: [
          {
            name: 'diyige'
          }
        ]
      }],      
      treeSetting: {
        check: {
          enable: true,
          chkStyle: "checkbox",
          chkboxType: {
            Y: "ps",
            N: "ps"
          }
        },
        view: {
          showIcon: false,
          showLine: false,
          showTitle: false,
          dblClickExpand: false,
          fontCss: function(treeId, node) {
            return node.fsuState === false
              ? { color: "#9A9A9A" }
              : { color: "#fff" };
          }
        },
        data: {
          key: {
            children: "children",
            name: "name",
            title: "code"
          }
        },
        callback: {
          onClick: (event, treeId, treeNode) => {
            if (treeNode.nocheck === false) {
              //如果这个节点是可选的，单击文字的时候将选中此项
              let treeObj = scope.$refs.tree.ztreeObj
              treeObj.checkNode(treeNode, !treeNode.checked, true, true);
            }
          },
          beforeCheck: (treeId, treeNode) => {
            if (this.curDistrict === 0) { return true; }
            let invalid = true;
            const traversal = node => {
              let parent = node.getParentNode()
              if (node.level >= this.curDistrict) {
                if (parent.children.filter(v => v.checked && (v.check_Child_State === 2 || v.check_Child_State === -1)).length != parent.children.length - 1) { invalid = false; return; }
                traversal(parent)
              }
              return true
            }
            if (
              !treeNode.checked &&
              treeNode.level >= this.curDistrict && 
              traversal(treeNode) && invalid
            ) {
              return false;
            }
            return true;
          }
          // beforeExpand: beforeExpand
        }
      },
      curView: '1',
      activeAlarmQuery: {},
      activeAlarmList: [{}, {}],
      activePagi: {
        page: 1,
        pageSize: 15,
        total: 0,
        handleCurrentChange: (v) => {
          this.activePagi.page = v;
          this.getActiveAlarmList();
        },
        pageSizeChange: () => {
          this.activePagi.pageSize = v;
          this.getActiveAlarmList();
        },
      },
      historyAlarmQuery: {},
      historyAlarmList: [{}, {}],
      historyPagi: {
        page: 1,
        pageSize: 15,
        total: 0,
        handleCurrentChange: (v) => {
          this.historyPagi.page = v;
          this.getHistoryAlarmList();
        },
        pageSizeChange: () => {
          this.historyPagi.pageSize = v;
          this.getHistoryAlarmList();
        },
      },
      activeColumnList: [
        {id: 'index', label: '序号', type: 'index', value: true},
        {id: 'xx1', label: '告警名称', prop: '告警值', value: true},
        {id: 'xx2', label: '告警值', prop: '告警等级', value: true},
        {id: 'xx3', label: '告警等级', prop: '企业名称', value: true},
        {id: 'xx4', label: '设备ID', prop: '站点层级', value: true},
        {id: 'xx5', label: '告警发生时间', prop: '站点名称', value: true},
        {id: 'xx6', label: '告警状态', prop: '楼层信息', value: true},
        {id: 'xx7', label: '设备信息', prop: '机房信息', value: true},
        {id: 'xx8', label: '告警确认状态', prop: '设备名称', value: true},
        {id: 'xx9', label: '操作', value: true, render: h => {
          return h(                    
            'el-table-column',
            {
              props: {
                label: '操作',
              },
              scopedSlots: resolveScopedSlots([{
                key: "default",
                fn: scope => {
                  return [
                    h('a', {
                      style: 'margin-right: 5px;',
                      on: {
                        click: () => {this.alarmConfirmToModal(scope.row)}
                      }
                    }, '告警确认'),
                    h('a', {
                      on: {
                        click () {this.deleteAlarmToModal(scope.row);}
                      }
                    }, '清除'),
                  ];
                }
              }])
            }
          )
        }},
      ],
      historyColumnList: [
        {id: 'xxx0', label: '序号', type: 'index', value: true},
        {id: 'xxx1', label: '告警名称', prop: '告警值', value: true},
        {id: 'xxx2', label: '告警值', prop: '告警等级', value: true},
        {id: 'xxx3', label: '告警等级', prop: '企业名称', value: true},
        {id: 'xxx4', label: '设备ID', prop: '站点层级', value: true},
        {id: 'xxx5', label: '告警发生时间', prop: '站点名称', value: true},
        {id: 'xxx6', label: '告警清除时间', prop: '楼层信息', value: true},
        {id: 'xxx7', label: '告警状态', prop: '机房信息', value: true},
        {id: 'xxx8', label: '设备信息', prop: '设备名称', value: true},
        {id: 'xxx9', label: '告警确认状态', prop: '告警确认状态', value: true},
      ],
      additionalColumnList: [],
      additionalInfoVisible: false,
      additionalInfoForm: {},
      alarmConfirmVisible: false,
      alarmConfirmForm: {},
    }
  },
  computed: {
    computedActiveColumnList () {
      return [{type: 'selection'}].concat(this.activeColumnList.filter(v => v.value))
    },
    computedHistoryColumnList () {
      return [{type: 'selection'}].concat(this.historyColumnList.filter(v => v.value))
    },
    additionalInfoRules () {
      return {
        aaa: {
          message: 'sdfse',
          trigger: 'blur',
          validator (rule, value, callback) {
            if (value) {
              callback()
            } else {
              callback(new Error(''))
            }
          }
        }
      }
    },
    alarmConfirmRules () {
      return {
        aaa: {
          message: 'sdfse',
          trigger: 'blur',
          validator (rule, value, callback) {
            if (value) {
              callback()
            } else {
              callback(new Error(''))
            }
          }
        }
      }
    },
    columnList () {
      return [{type: 'selection'}].concat(this.activeColumnList.map(v => v).concat(this.additionalColumnList));
    },
  },
  methods: {
    getUniqueServiceList () {
      this.$api.enterpriseLevelControllerGetUniqueServiceList().then(res => {
        this.enterPriseList = res;
      })
    },
    getServerList (enterpriseCode) {
      return (this.enterPriseList.find(v => v.enterpriseCode === enterpriseCode) || {serverCodes: []}).serverCodes
    },
    getActiveAlarmList () {

    },
    getHistoryAlarmList () {

    },
    additionalInfoToModal () {
      this.additionalInfoVisible = true;
    },
    additionalInfoSubmit () {
      let query = {
        serverCode: this.additionalInfoForm.serverCode,
        enterpriseCode: this.additionalInfoForm.enterpriseCode,
        proStrList: [this.additionalInfoForm.str],
        proNameList: [this.additionalInfoForm.name],
        proTypeList: [this.additionalInfoForm.type],
      }
      this.$api.alarmauxilaryControllerAdd(query).then(res => {
        console.log(res);
      })
    },
    alarmConfirmToModal (alarm) {
      let list;
      if (!alarm) {
        list = this.$refs.activeAlarmList.$refs.table.selection;
        if (list.length < 1) {
          this.$message({
            type: 'warning',
            message: '请至少选择一条告警'
          })
          return;
        }
      }
      this.alarmConfirmAlarmList = list || [alarm];
      this.alarmConfirmVisible = true;
    },
    alarmConfirmSubmit () {},
    getAdditionalList () {
      this.$api.alarmauxilaryControllerList({
        enterpriseCode: this.curEnterprise.code,
        serverCode: this.curServer.code
      }).then(res => {
        setTimeout(() => {
          this.additionalColumnList = [
            {id: 'test', label: 'test', prop: 'test', value: true},
          ]
        }, 1000);
        
        // this.$refs.activeAlarmList.loadCustom()
        // this.$refs.historyAlarmList.loadCustom()
      })
    },
    deleteAlarmToModal (alarm) {
      this.$confirm(`确定要删除选中的数据吗？<br/>注意：该内容删除之后无法恢复！`, '是否删除', 
      { type: 'warning', dangerouslyUseHTMLString: true }).then(res => {
        
      }).catch(() => {

      })
    },
  },
  mounted () {
    this.getUniqueServiceList();
    this.getAdditionalList();
  }
}
</script>

<style lang="less" scoped>
#alarm-list-page{
  background-color: #011838;
  .left-tree{
    width: 210px;
    margin: 10px;
    margin-right: 0;
    padding: 10px;
    height: calc(100% - 40px);
    background-color: #0B1D3B;
  }
  .right-content{
    margin: 10px;
    flex: 1;
    height: calc(100% - 20px);
    background-color: #0B1D3B;
    &>.el-tabs{
      height: 100%;
    }
  }
  .query-form{
    min-height: 50px;
    width: 100%;
    margin-bottom: 16px;
  }
  .list{
    flex: 1;
    overflow: hidden;
    padding-bottom: 10px;
    .table-box{
      flex: 1;
      overflow: hidden;
    }    
  }
}
</style>


