<template>
  <section class="custom-column">
    <el-button type='primary' size="mini" @click="customColumnToModal()"><slot></slot></el-button>
    <el-dialog
      title="自定义列"
      class="m400"
      :visible="customColumnVisible">

      <draggable
        :list="columnListForSorting"
        class="list-group"
        ghost-class="ghost" >
        <transition-group tag="ul">
          <li v-for="item in columnListForSorting" :key="item.id" class="item">
            <div class="holder">
              <el-checkbox v-model="item.value">{{ item.label }}</el-checkbox>
            </div>
          </li>
        </transition-group>
      </draggable>

      <span slot="footer" class="dialog-footer">
        <el-button size="mini" type="default" @click="customColumnVisible = false">取 消</el-button>
        <el-button size="mini" type="primary" @click="customColumnVisible = false; saveCustomColumn();">确 定</el-button>
      </span>
    </el-dialog>
  </section>
</template>

<script>
import draggable from 'vuedraggable'
export default {
  name: 'custom-column',
  components: {
    draggable,
  },
  props: {
    value: Array,
    name: String,
  },
  data () {
    return {      
      customColumnVisible: false,
      columnListForSorting: [],
    }
  },
  methods: {
    customColumnToModal () {
      this.customColumnVisible = true;
      this.columnListForSorting = this.value.map(v => Object.assign({}, v));
    },
    loadCustom () {
      let loadData = localStorage[this.name] || JSON.stringify(this.value.map(v => v.id + '-1'));
      loadData = JSON.parse(loadData);
      let cl = []
      loadData.forEach((v, i) => {
        let item = this.value.find(u => u.id === v.split('-')[0]);
        cl[i] = item;
        cl[i].value = !!(+v.split('-')[1]);
      })
      this.$emit('input', cl)
    },
    saveCustomColumn () {
      let loadData = this.calcLoadData(this.columnListForSorting)
      localStorage[this.name] = loadData;
      this.$emit('input', this.columnListForSorting)
    },
    calcLoadData (list) {
      return JSON.stringify(list.map(v => v.id + '-' + (~!v.value + 2)));

    },
  },
  mounted () {
    this.$watch('value', (n, o) => {
      if (this.calcLoadData(n) !== this.calcLoadData(o)) {
        this.loadCustom();
      }
    })
    this.loadCustom();
  }
}


// https://blog.csdn.net/weixin_34221112/article/details/91430657
const resolveScopedSlots = function (
  fns, // 为一个对象数组，见上文scopedSlots
  res
) {
  res = res || {};
  for (var i = 0; i < fns.length; i++) {
    if (Array.isArray(fns[i])) {
      // 递归调用
      resolveScopedSlots(fns[i], res);
    } else {
      res[fns[i].key] = fns[i].fn;
    }
  }
  return res
}

const customTable = {
  name: 'custom-table',
  render (h) {
    let scope = this;
    return this.visible && h(
      'el-table',
      {
        attrs:{ 
          data: scope.data,
          height: scope.height,
        },
        ref: "table"
      },
      (scope.columns || []).map(v => {
        if (v.render) {
          return v.render(h)
        }
        return h(
          'el-table-column',
          {
            attrs: {
              prop: v.prop,
              label: v.label,
              type: v.type,
              formatter: scope.nullFilter,
            }
          }
        )
      })
    )
  },
  props: {
    height: '',
    data: null,
    columns: Array,
  },
  data () {
    return {
      visible: true,
    };
  },
  methods: {
    nullFilter (a, b, value) {
      if (value === null || value === undefined || value === "" || (Array.isArray(value) || value.length === 0)) return '-'
      return value
    },
  },
  mounted () {
    this.$watch('columns', function () {
      this.visible = false;
      setTimeout(() => {
        this.visible = true;
      }, 0);
    })
  }
}

export {customTable, resolveScopedSlots}

</script>

<style lang="less" scoped>
  .holder{
    padding: 5px 20px;
    border: 1px solid #aaa;
    border-radius: 5px;
    margin-bottom: 10px;
  }
  .custom-column{
    display: inline-block;
  }
</style>


